.nav {
  &__list {
    @include flex-v-center;

    @include high-tablet {
      @include flex-all-center;
      flex-direction: column;
      width: 100%;
      height: 100%;
    }

  }

  &__item {
    &:not(:last-child) {
      margin-right: 40px;

      @include high-tablet {
        margin-right: 0;
        margin-bottom: 30px;
      }
    }


  }

  &__link {
    font-weight: 400;
    font-size: 20px;
    line-height: 160%;
    text-align: center;
    color: var(--light-color);

    @include high-tablet {
      font-weight: 600;
      font-size: 30px;
      line-height: 160%;
    }
  }


  @include high-tablet {
    position: fixed;
    left: 0;
    top: 0;
    z-index: 500;
    overflow-y: auto;
    width: 100%;
    height: 100vh;
    background-color: var(--dark-color);
    opacity: 0;
    visibility: hidden;
    transition: all 0.3s;

  }


}

.menu--active {
  opacity: 1;
  visibility: visible;
}

.phone {
  font-weight: 600;
  font-size: 30px;
  line-height: 160%;
  text-align: right;
  color: var(--light-color);
}
