.tariffs {
  padding-top: 0;

  &__title {
    font-weight: 600;
    font-size: 50px;
    line-height: 160%;
    color: var(--dark-color);
    padding-bottom: 60px;

    @include mobile {
      text-align: center;

      font-weight: 600;
      font-size: 30px;
      line-height: 140%;
    }
  }

  &__list {
    display: grid;
    grid-template-columns: repeat(12, 1fr);
    gap: 40px;

    @include high-tablet {
      gap: 20px;
    }

    @include small-tablet {
      grid-template-columns: repeat(8, 1fr);
    }
  }

  &__item {
    display: flex;
    flex-direction: column;
    align-items: center;
    grid-column: 4 span;
    border-radius: 20px;
    border: 1px solid rgba(20, 16, 36, 0.2);
    box-sizing: border-box;
    padding: 40px;

    &--current {
      border: 1px solid var(--dark-color);
      background-color: var(--dark-color);
      transform: scale(1.1);

      .tariffs__subtitle {
        color: var(--light-color);
      }

      .tariffs__price {
        color: var(--light-color);
      }

      .tariffs__descr {
        color: var(--light-color);
      }

      @include high-desktop {
        transform: scale(1);
      }


    }

    @include small-tablet {
      grid-column: 8 span;
    }
  }

  &__subtitle {
    margin: 0;
    margin-bottom: 60px;
    text-align: center;
    font-weight: 600;
    font-size: 30px;
    line-height: 160%;
    text-align: center;
    color: var(--dark-color);

  }

  &__price {
    font-weight: 600;
    font-size: 50px;
    line-height: 160%;
    color: var(--dark-colors);
    margin-bottom: 20px;

    @include high-desktop {
      font-size: 45px;
    }

    @include high-tablet {
      font-size: 30px;
    }
  }

  &__descr {
    margin: 0;
    text-align: center;
    font-weight: 400;
    font-size: 18px;
    line-height: 160%;
    color: var(--dark-color);
    margin-bottom: auto;

  }

  &__btn {
    margin-top: 60px;
    min-width: 100%;
    cursor: pointer;

    @include high-tablet {
      padding: 10px 20px;
      font-size: 16px;
    }
  }
}
